.answer
  border: 2px solid gray
  display: grid
  grid-template-columns: 270px 120px 160px
  // width: 450px
  margin: 3px
  justify-content: center


.answer p
  padding: 6px
  margin: initial
  border: 1px dashed black
  background-color: rgba(0,0,0, .5)

.wrapOl
  display: grid
  grid-template-columns: 40px 110px 100px
  // justify-content: center
ol
  margin: 3px
  padding: initial
  padding: 3px 7px
  // width: 210px
  border: 1px dashed gray
  background-color: rgba(0,0,0, .5)

ol p
  margin: initial

.tittleParagraph
  white-space: nowrap
  cursor: pointer
  color: #eeefe7
  text-align: left
  

.tittleParagraphImportantColor
  // white-space: nowrap
  // cursor: pointer
  color: var(--color) //#fa6d63
  text-align: left

.tittleParagraph:hover
  color: #73fdbf
  
.cheked
  color: #73fdbf
  text-decoration: underline
  text-align: left
  font-size: 1.1em

.titleNumber
  color: #eeefe7
  

.answerHeight h3
  color: #eeefe7
  text-align: center

.measureColor
  color: #a4ee89