.IMG
  min-width: 200px
  max-width: 550px
  max-height: 460px 
  border-radius: 12px
  background: rgba(0,0,0,.3)
  transition: all .5s cubic-bezier(.1,.75,.15,.84)
  opacity: 1
  transform: scale(1, 1)
  left: 0
  position: relative
  padding: 20px
  margin: 16px
  // background-image: url('../../images/logo.png')
  background-repeat: no-repeat
  background-size: cover

.IMG2
  min-width: 200px
  max-width: 550px
  position: relative
  max-height: 460px 
  border-radius: 100%
  background: #000
  transition: all .5s cubic-bezier(.1,.75,.15,.84)
  opacity: 0
  transform: scale(.2, .2)
  left: -200px
  padding: 20px
  margin: 16px
  

.hintImageWrapper
  min-width: 650px
  max-width: 550px
  height: auto
  // min-height: 500px
  // height: auto
  // border: 1px solid green
  // background-image: url('../../images/mainBg.jpg')
  background-repeat: no-repeat
  background-size: cover
  background-position: 0 -100px
  // color: rgba(0, 0, 0, 0.9)
  position: relative
  outline: 1px dashed gray
  // z-index: 1

.hintImageWrapper:before
  z-index: 0
  content: ''
  width: 100%
  height: 100%
  position: absolute
  top: 0
  left: 0
  right: 0
  bottom: 0
  background: black
  opacity: 0.6
.advantages
  width: auto
  position: absolute
  z-index: 1
  margin: auto
  color: #61dafb
  height: inherit
.advantages h3

.advantages ul
  text-align: left
  transition: all .7s
  position: relative
  opacity: 1
  visibility: visible
  list-style-type: disc
  line-height: 1.2em
  margin: initial
.advantages ul li
  padding-bottom: 14px
.advantagesDisable
  position: absolute
  // right: 100px
  // transform: translate(1000px, 0)
  transition: all .5s cubic-bezier(.1,.75,.15,.84)
  opacity: 0
  visibility: hidden
  // height: 0
.hintImageWrapper button
  position: absolute
  left: 242px
  top: -95px
  z-index: 2
  padding: 5px
  font-family: monospace
  font-weight: bold
  padding: 10px 20px
  width: 120px
  background-color: transparent
  border-radius: 4px
  // background: linear-gradient(#e66465, #9198e5)
  background: linear-gradient(.35turn, #61dafb, gray)
  transition: all .2s ease
  // opacity: .6
  // opacity: .8
  color: #000
.hintImageWrapper button:hover
  color: #fff
  background: linear-gradient(.35turn,gray, #61dafb)
  transition: all .2s ease
  background-position: 116px
.hintImageWrapper button:active
  transform: scale(.94,.94)
  color: aqua